<template>
    <div class="fullscreen">
        <!--<p class="decode-result">-->
            <!--Last result: <b>{{ result }}</b>-->
        <!--</p>-->
        <!--<p class="decode-result">-->
            <!--Last error: <b>{{ error }}</b>-->
        <!--</p>-->
            <qrcode-stream
                    @detect="onDetect"
                    @init="onInit"
                    @decode="onDecode"
                    :track="this.paintBoundingBox "
                    :key="_uid"
                    style="height:100%"

            >


            </qrcode-stream>
        <!--:track="paintBoundingBox"-->
    </div>
</template>

<script>
    import { QrcodeStream } from "vue-qrcode-reader";
    import { eventBus } from "../main";

    export default {
        components: {
            QrcodeStream,
        },
        data() {
            return {
                camera: this.paintBoundingBox(),
                // const options = [
                //     { text: "nothing (default)", value: undefined },
                //     { text: "outline", value: this.paintOutline },
                //     { text: "centered text", value: this.paintCenterText },
                //     { text: "bounding box", value: this.paintBoundingBox },
                // ]
            };
        },
        methods: {
            onDecode(decodedString) {
                console.log(decodedString, );
            },
            async onInit(promise) {
                try {
                    await promise;
                    console.log(promise);
                } catch (error) {
                    if (error.name === "NotAllowedError") {
                        this.error = "ERROR: you need to grant camera access permisson";
                        console.log(this.error);
                    } else if (error.name === "NotFoundError") {
                        this.error = "ERROR: no camera on this device";
                    } else if (error.name === "NotSupportedError") {
                        this.error = "ERROR: secure context required (HTTPS, localhost)";
                    } else if (error.name === "NotReadableError") {
                        this.error = "ERROR: is the camera already in use?";
                    } else if (error.name === "OverconstrainedError") {
                        this.error = "ERROR: installed cameras are not suitable";
                    } else if (error.name === "StreamApiNotSupportedError") {
                        this.error = "ERROR: Stream API is not supported in this browser";
                    }
                }
            },
            async onDetect(promise) {
                console.log(promise);
                try {
                    const { content, location } = await promise;
                    console.log(content, location);
                    this.result = atob(content);
                    const qrData = this.result;
                    eventBus.qrRead(qrData);
                } catch (e) {
                    console.log('onDetect',e);
                }
                // Promise.resolve(Promise).then(function (data) {
                //     console.log(data.toJSON())
                //
                // });

                // this.result = result.;
            },

            paintBoundingBox (detectedCodes, ctx) {
                for (const detectedCode of detectedCodes) {
                    const { boundingBox: { x, y, width, height } } = detectedCode
                    ctx.lineWidth = 2;
                    ctx.strokeStyle = '#007bff'
                    ctx.strokeRect(x, y, width, height);

                }
            },
        },
    }
</script>
